(function() {
  'use strict';

  angular
    .module('virtualQueue')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('home', {
        url: '/Side',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      })
      .state('new', {
        url: '/queues?orgId',
        templateUrl: 'app/newQ/newqueue.html',
        controller: 'NewController',
        controllerAs: 'new'
      });
    $urlRouterProvider
        .otherwise('/');
  }

})();

